<template>
  <div style="margin: 25px">
    <el-card style="height: 800px">
      <el-breadcrumb separator-class="el-icon-arrow-right" style="height: 40px">>
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <el-form  :inline="true" size="small" :model="formline" label-width="90px">
        <el-form-item label="">
          <el-date-picker
            v-model="formline.startDate"
            type="date"
            format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" label-width="25px">
          <el-date-picker
            v-model="formline.endDate"
            type="date"
            format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search()"></el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="warning" @click="revise = true"></el-button>
        </el-form-item>

      </el-form>

      <div style="height: 30px"></div>
      <el-table  :data="tableData" border highlight-current-row style="width: 100%">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column  prop="classPeriodId" label="" width="100"></el-table-column>
        <el-table-column  prop="userName" label="" width="150"></el-table-column>
        <el-table-column  prop="className" label="" width="150"></el-table-column>
        <el-table-column  prop="goToWorkTime" label="" width="150"></el-table-column>
        <el-table-column  prop="timeFromWork" label="" width="150"></el-table-column>
        <el-table-column  prop="startChargeTime" label="" width="150"></el-table-column>
        <el-table-column  prop="endChargeTime" label="" width="150"></el-table-column>
        <el-table-column  prop="classDate" label="" width="200"></el-table-column>

        <el-table-column
          prop=""
          label="">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="cancel(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="deal(scope.row)"></el-button>
          </template>

        </el-table-column>
      </el-table>
      <el-pagination @current-change="handleCurrentChange" background layout="prev, pager, next":total="total">
      </el-pagination>

      <el-dialog title="" :visible.sync="revise" width="65%">
        <el-form :model="form">
          <el-form-item label="" :label-width="addWidth">
            <el-input v-model="form.username"></el-input>
          </el-form-item>
          <el-row type="flex">
            <el-col :span="10">
              <el-form-item label=""  :label-width="addWidth">
                <el-time-select
                  v-model="form.startTime" :picker-options="{ start: '07:00', step: '00:15', end: '24:00' }" placeholder="">
                </el-time-select>
              </el-form-item>
            </el-col>

            <el-col :span="10">
              <el-form-item label=""  :label-width="addWidth">
                <el-time-select
                  v-model="form.endTime" :picker-options="{ start: '07:00', step: '00:15', end: '24:00' }" placeholder="">
                </el-time-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row type="flex">
            <el-col :span="10">
              <el-form-item label=""  :label-width="addWidth">
                <el-time-select
                  v-model="form.startTimeCard" :picker-options="{ start: '07:00', step: '00:15', end: '24:00' }" placeholder="">
                </el-time-select>
              </el-form-item>
            </el-col>

            <el-col :span="10">
              <el-form-item label=""  :label-width="addWidth">
                <el-time-select
                  v-model="form.endTimeCard" :picker-options="{ start: '07:00', step: '00:15', end: '24:00' }" placeholder="">
                </el-time-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row type="flex">
            <el-col :span="10">
              <el-form-item label="" :label-width="addWidth">
                <el-input v-model="form.classname"></el-input>
              </el-form-item>
            </el-col>

          </el-row>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="revise = false"> </el-button>
          <el-button type="primary" @click="submitForm('form')"> </el-button>
        </div>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
  import ElForm from "../../../../node_modules/element-ui/packages/form/src/form";

  function getHoursTime(date) {
    if(date==""){return null}
    var newDate = new Date(date);
    var year = newDate.getFullYear();
    var month = newDate.getMonth() + 1;
    var day = newDate.getDate();
    var  hours=newDate.getHours();
    var  minute=newDate.getMinutes();
    var  seconds=newDate.getSeconds();
    return year + "-" + month + "-" + day+":"+hours+":"+minute+":"+seconds;
  }

  function getDateTime(date) {
    if(date==""){return null}
    var newDate = new Date(date);
    var year = newDate.getFullYear();
    var month = newDate.getMonth() + 1;
    var day = newDate.getDate();
    return year + "-" + month + "-" + day;
  }
  export default {

    components: {ElForm},
    data() {
      return {
        dialogName:'aa',
        checkList: ['',],
        tableData: [{

        }],
        formline: {
          startDate: '',
          endDate:'',
          page:1,
          total:0,
        },

        form: {
          username: '',
          classname:'',
          startTime:'',
          endTime:'',
          startTimeCard:'',
          endTimeCard:''
        },



        value: '',
        revise:false,
        add: false,
        addWidth: '120px',

      }
    },
    methods: {
      //
      handleCurrentChange(val){
        this.page = val;
        this.search();
      },

      search:function () {
        var formData={
          startDate:getDateTime(this.formline.startDate),
          endDate:getDateTime(this.formline.endDate),
          pageIndex:this.page,
        }
        var qs = require('querystring')
        var _self = this;
        this.$ajax.post('/cms-attendance-web/selectClass',
          qs.stringify(formData)).then(function(result){
          var list = result.data.list;
          _self.total=result.data.page.record*10;
          for(var i=0;i<list.length;i++){
            list[i].classDate = getDateTime(list[i].classDate);
          }
          _self.tableData=result.data.list;
        })
      },


      submitForm: function (form) {

        var formData = {
          userName: this.form.username,
          className: this.form.classname,
          goToWorkTime:this.form.startTime,
          timeFromWork: this.form.endTime,
          startChargeTime:this.form.startTimeCard,
          endChargeTime:this.form.endTimeCard,

        }

        var qs = require('querystring')
        var _self = this;
        this.$ajax.post('/cms-attendance-web/addClass',
          qs.stringify(formData)).then((response) => {
           _self.revise = false
          if (response.data.num === 1) {
            this.$message.success('')
          } else {
            this.$message.success('')

          }
        })

      },
      //
      deal(row){
        var r = confirm("?");
        if(r==false){return}
        var formData={
          classPeriodId:row.classPeriodId,
        }
        var _self = this;
        var qs = require('querystring');
        this.$ajax.post('/cms-attendance-web/updateClass',
          qs.stringify(formData)).then((response) => {
          self.revise = false
          if (response.data.num === 1) {
            this.$message.success('')

          } else {
            this.$message.success('')

          }
          _self.search();
        })
      },
      //
      cancel(row){
        var r = confirm("?");
        if(r==false){return}
        var formData={
          classPeriodId:row.classPeriodId,
        }
        var _self = this;
        var qs = require('querystring');
        this.$ajax.post('/cms-attendance-web/deleteClass',
          qs.stringify(formData)).thenthen((response) => {
          self.revise = false
          if (response.data.num === 1) {
            this.$message.success('')
          } else {
            this.$message.success('')
          }
          _self.search();
        })
      }
    }

  }
</script>

<style>
  .el-checkbox+.el-checkbox {
    margin-right: 20px;
    margin-left: 0px;
  }
</style>



